@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
/* Linha de lista de filmes */
.movieRow{
     margin-bottom: 30px;
}

.movieRow h2{
     margin: 0px 0px 0px 30px;
}

/* √Årea da lista */
.movieRow--listarea{
     overflow-x: hidden;
     padding-left: 30px;
}

/* Lista */
.movieRow--list{
     transition: all ease-in-out 500ms;
}


/* Item */
.movieRow--item{
     display: inline-block;
     width: 150px;
     cursor: pointer;
}
.movieRow--item img{
     width: 100%;
     transform: scale(0.9);
     transition: all ease-in-out 250ms;
}

.movieRow--item img:hover{
     transform: scale(1);
}

.movieRow--left,
.movieRow--right{
     position: absolute;
     width: 40px;
     height: 225px;
     background-color: rgba(0,0,0,0.5);
     z-index: 99;
     display: flex;
     align-items: center;
     justify-content: center;
     overflow: hidden;
     cursor: pointer;
     opacity: 0;
     transition: all ease-in-out 500ms;
}

.movieRow--left{
     left: 0;
}
.movieRow--right{
     right: 0;
}

.movieRow:hover .movieRow--left,
.movieRow:hover .movieRow--right{
     opacity: 1;
}


@media(max-width: 768){
     .movieRow--left,
     .movieRow--right{
          opacity: 1;
     }
}
.featured{
     height: 100vh;
}

.featured--vertical{
     width: inherit;
     height: inherit;
     background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal{
     width: inherit;
     height: inherit;
     background: linear-gradient(to right, #111 30%, transparent 70%);
     display: flex;
     flex-direction: column;
     justify-content: center;
     padding-left: 30px;
     padding-bottom: 200px;
}

.featured--name{
     font-size: 60px;
     font-weight: bold;
}

.featured--info{
     font-size: 18px;
     font-weight: bold;
     margin-top: 15px;
}

.featured--score,
.featured--year,
.featured--seasons{
     display: inline-block;
     margin-right: 15px;
}

.featured--score{
     color: #46d369;
}

.featured--description{
     margin-top: 15px;
     font-size: 20px;
     color: #999999;
     max-width: 40%;
}

.featured--buttons{
     margin-top: 15px;
}

.featured--watchbutton,
.featured--mylistbutton{
     display: inline-block;
     font-size: 20px;
     font-weight: bold;
     padding: 12px 25px;
     border-radius: 5px;
     text-decoration: none;
     margin-right: 10px;
     opacity: 1;
     transition: all ease-in-out 250ms;
}

.featured--watchbutton:hover,
.featured--mylistbutton:hover{
     opacity: 0.7;
}

.featured--watchbutton{
     background-color: #fff;
     color: #000;
}

.featured--mylistbutton{
     background-color: #333;
     color: #fff;
}

.featured--genres{
     margin-top: 15px;
     font-size: 18px;
     color: #999999;
}

@media(max-width: 768){
     .featured{
          height: 90vh;
     }
     .featured--name{
          font-size: 40px;
     }
     .featured--info{
          font-size: 16px;
     }
     .featured--description{
          font-size: 14px;
          max-width: 100%;
          margin-right: 30px;
     }
     .featured--watchbutton,
     .featured--mylistbutton{
          font-size: 16px;
          padding: 10px 20px;
     }
     .featured--genres{
          font-size: 14px;
     }
}
header{
     position: fixed;
     z-index: 999;
     top: 0;
     left: 0;
     right: 0;
     height: 70px;
     display: flex;
     justify-content: space-between;
     align-items: center;
     padding: 0 30px;
     background: transparent;
     transition: all ease-in-out 250ms;
}

header.black{
     background: #141414;
}

.header--logo{
     height: 25px;
}

.header--logo img{
     height: 100%;
}

.header--user{
     height: 35px;
}

.header--user img{
     height: 100%;
     border-radius: 5px;
}
footer{
     text-align: center;
     margin: 50px;
}

footer a{
     text-decoration: none;
     font-size: 18px;
     color: #f0f0f0;
     transition: all ease-in-out 250ms;
}

footer a:hover{
     color: #E50914;
}
*{
     background-origin: border-box;
     font-family: "Roboto", sans-serif;
}

body{
     background-color: #111;
     color: #fff;
     margin: 0;
}

.lists{
     margin-top: -150px;
}

body::-webkit-scrollbar{
     width: 0.25rem;
}

body::-webkit-scrollbar-track{
     background: #202020;
}

body::-webkit-scrollbar-thumb{
     background: #333333;
}

.loading{
     position: fixed;
     left: 0;
     top: 0;
     right: 0;
     bottom: 0;
     z-index: 99;
     background-color: #000;
     display: flex;
     justify-content: center;
     align-items: center;
}
